@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
html {
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Nunito Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: hsl(0, 0%, 94%);
  width: 100vw;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.flex{
  display: flex;
}
.space-around{
  justify-content: space-around;
}
.column{
  flex-direction: column;
}


.switch{
    display: flex;
    justify-content: space-between;
    width: 8%;
    margin-right: 2rem;
}

.moon-icon{
    position: relative;
    top: 1.4rem;
    outline: black;
    color: white;
    -webkit-filter: drop-shadow(0px 0px 2px black);
            filter: drop-shadow(0px 0px 2px black);
    cursor: pointer;

}

@media screen and (max-width:1024px) {
    .switch{
        width: 11%;
        margin-right: 3rem;
    }
}
@media screen and (max-width:768px){
    .switch{
        width: 14%;
        margin-right: 5.5rem;
    }
}
@media screen and (max-width:425px){
    .switch{
        width: 24%;
        margin-right: 1rem;
    }
}
@media screen and (max-width:375px){
    .switch{
        width: 27%;
        margin-right: 0rem;
    }
}
@media screen and (max-width:320px){
    .switch{
        width: 32%;
        margin-right: -0rem;
    }
}
.nav{
    background-color: white; 
    box-shadow: -1px 3px 3px 0px hsl(210, 4%, 69%);
    -webkit-box-shadow: -1px 3px 3px 0px hsl(210, 4%, 69%);
    -moz-box-shadow: -1px 3px 3px 0px hsl(210, 4%, 69%);
}
.nav-items{
  display: flex;
  justify-content: space-between;
}
.nav-items h1{
    margin-left: 3rem;
    font-size: 1.5rem;
}
@media screen and (max-width:375px){
  .nav-items h1{
    font-size: 1.3rem;
  }
}

@media screen and (max-width:375px){
  .nav-items h1{
    font-size: 1rem;
    margin-top: 19px;
  }
}
.countries{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(18rem, 1fr));
    justify-content: center;
}
.countries-container{
    padding-left: 3rem;
    padding-bottom: 4rem;

}
.countries-data{
    padding-left: 0.5rem; 
}

.countries-container-individual:hover{
    cursor: pointer;
}

.countries-container-individual{    
    background-color: white;
    width: 250px;
    box-shadow: 1px 9px 15px -5px rgba(0,0,0,0.83);
    -webkit-box-shadow: 1px 7px 13px -5px rgba(0,0,0,0.83);
    -moz-box-shadow: 1px 9px 15px -5px rgba(0,0,0,0.83);    
    border-radius: 0 0 10px 10px;
    padding-bottom: 3rem;

}
.countries-data h2{
    width: 250px;
    font-size: 1.3rem;
    padding-top: 1.2rem;
}
.countries-data-result{
    color:hsl(200, 15%, 8%)
}
.countries-data-items{
    font-weight: 600;
}

.flag{
    width: 250px;
    height: 160px;
}
.countries-link{
    text-decoration: inherit;
    color: inherit;
}

.searchInput{
    width: 25rem;
    margin-left: 3rem;
    margin-top: 3rem;
    margin-bottom: 3rem;
    height: 2.5rem;
    border-radius: 5px;
    border: 1px solid gray;
    outline: none;
    font-size: 0.9rem;
    color: black;

    /*filter: drop-shadow(0 0 0.2rem rgb(124, 121, 121));*/
  
  }
  .searchInput:focus{
      border: 1px solid gainsboro;

  }
  .searchInput::-webkit-input-placeholder {
      padding-left: 2rem;
  }
  .searchInput::placeholder, .searchInput::-webkit-input-placeholder {
      padding-left: 2rem;
  }
  .search{
      position: absolute;
      top: 7.9rem;
      left: 3.3rem;
      visibility: visible;
  }

  @media screen and (max-width:425px){
    .searchInput{
        width: 20rem;
    }
}
  
body{
    background-color: white;

}
.button-back{
    margin-top: 3rem;
    margin-bottom: 3rem;
    margin-left: 3rem;
    width: 5rem;
    background-color: white;
    border-radius: 5px;
    outline: none;
    border: 2px solid gainsboro;
    -webkit-filter: drop-shadow(0px 2px 2px gray);
            filter: drop-shadow(0px 2px 2px gray);
    cursor: pointer;

}
.button-back:hover{
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    -webkit-transition: all 3s ease-in-out;
    -moz-transition: all 1s ease-in-out;
}

.arrow-left{
    position: relative;
    left: -10px;
}


.country-info-main-container{
    padding-bottom: 10rem;
    overflow-y: hidden;
}

.country-info-container{
    margin-left: 5rem;
    display: flex;
    justify-content: space-evenly;
}
.country-info-details{
    display: flex;
    justify-content: space-evenly;
    width: 35rem;

}
.country-info-container-details{
    display: flex;
    flex-direction: column;

}

.country-info-flag{
    width: 30rem;
    margin-left: -9rem;
}

.country-info-text-2{
    margin-top: 3rem;
}

.border-countries{
    width: 4rem;
    height: 2rem;
    text-align: center;
    border: 1px solid gray;
    border-radius: 5px;
    position: relative;
    left: 2rem;
}
.country-info-borders{
    grid-gap: 10px;
    position: relative;
    left:4rem ;
    display: grid;
    width: 38rem;
    
}
.border-countries-text{
    font-size: 0.8rem;
    position: relative;
    top:-5px;   
}
.country-info-borders h3{
    width: 11rem;
    position: relative;
    left: -0.2rem;
}
.country-info-borders-details{
    display: grid;
    grid-template-columns: repeat(5, minmax(1rem, 5rem));
    grid-gap: 10px;
    justify-content: end;
    position: relative;
    top: -3.5rem;

}

@media screen and (max-width:1024px){
    .country-info-flag{
        margin-left: -2rem;
        width: 23rem;
    }
}

@media screen and (max-width:768px){
    .country-info-container{
        flex-direction: column;
    }
    .country-info-container-details{
        position: relative;
        left: -5.5rem;
    }
    .country-info-details{
        width: 27rem;
        position: relative;
        left: 3rem;
    }
    .country-info-borders-details{
        display: grid;
    }
}

@media screen and (max-width:425px){
    .country-info-borders-details{
        grid-template-columns: repeat(3, minmax(1rem, 5rem));
        position: relative;
        left: -14rem;

    }
    .country-info-details{
        flex-direction: column;
    }
    .country-info-borders h3{
        position: relative;
        left: -1rem;
    }
}
@media screen and (max-width:425px){
    .country-info-flag{
        width: 19rem;
    }
    .country-info-borders-details{
        top: -0.5rem;
        left: -25rem;
    }
    
}

@media screen and (max-width:320px){
    .country-info-flag{
        width: 15rem;
    }
}
